body {
  text-align: center;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #00ABE1;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}

.logo-section {
  display: flex;
  margin-left: 20px;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  padding: 0 8px;
}

.nav-links {
  display: flex;
  list-style: none;
  padding: 0 20px;
}

.nav-links li {
  margin: 0 15px;
}

.nav-links a {
  text-decoration: none;
  color: white;
}

#simple-menu a {
  text-decoration: none;
}

#simple-menu a:visited {
  color: #333;
}

.row {
  text-align: center;
  display: flex;
  justify-content: center;
}

.row>div {
  display: block;
  width: 56px;
  height: 56px;
  border: 1px solid #bbb;
  margin: 4px;
  text-align: center;
  line-height: 60px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.5em;
}

.row>div.green {
  --background: #5ac85a;
  --border-color: #5ac85a;
  animation: flip 0.5s ease forwards;
}

.row>div.grey {
  --background: #a1a1a1;
  --border-color: #a1a1a1;
  animation: flip 0.6s ease forwards;
}

.row>div.yellow {
  --background: #e2cc68;
  --border-color: #e2cc68;
  animation: flip 0.5s ease forwards;
}

.row>div:nth-child(2) {
  animation-delay: 0.2s;
}

.row>div:nth-child(3) {
  animation-delay: 0.4s;
}

.row>div:nth-child(4) {
  animation-delay: 0.6s;
}

.row>div:nth-child(5) {
  animation-delay: 0.8s;
}

/* keyframe animations */
@keyframes flip {
  0% {
    transform: rotateX(0);
    background: #fff;
    border-color: #333;
  }

  45% {
    transform: rotateX(90deg);
    background: white;
    border-color: #333;
  }

  55% {
    transform: rotateX(90deg);
    background: var(--background);
    border-color: var(--border-color);
  }

  100% {
    transform: rotateX(0deg);
    background: var(--background);
    border-color: var(--border-color);
    color: #eee;
  }
}


.row.current>div.filled {
  animation: bounce 0.2s ease-in-out forwards;
}

@keyframes bounce {
  0% {
    transform: scale(1);
    border-color: #ddd;
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
    border-color: #333;
  }
}


.keypad {
  max-width: 500px;
  margin: 20px auto;
}

.keypad>div {
  margin: 4px;
  width: 40px;
  height: 50px;
  background: #eee;
  display: inline-block;
  border-radius: 6px;
  line-height: 50px;
}

.keypad>div.green {
  background: #5ac85a;
  color: #fff;
  transition: all 0.3s ease-in;
}

.keypad>div.yellow {
  background: #e2cc68;
  color: #fff;
  transition: all 0.3s ease-in;
}

.keypad>div.grey {
  background: #a1a1a1;
  color: #fff;
  transition: all 0.3s ease-in;
}

.MuiModal-root {
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  position: relative;
  margin: auto;
  width: 90%;
  max-width: 600px;
  overflow-y: auto;
  outline: none;
  background: white;
  box-shadow: 0px 0px 10px #454545;
  border-radius: 10px;
}

.modal>h4 {
  /* background: #17D72A; */
  margin: 0;
  padding: 20px 0;
  color: white;
}

.modal>.modal-main-content {
  padding: 0 24px 24px;
  text-align: left;
}

.modal>.modal-main-content h6 {
  margin-top: 16px;
}

.modal .win-message {
  background: #17D72A;
}
.modal .lose-message {
  background: darkslategrey;
}
.modal #modal-modal-description {
  padding: 16px 0 8px;
}

